import { Dialog, Transition } from "@headlessui/react";
import { Fragment, useState } from "react";
import { AddToSignerInterface } from "../scheme";
import { DeviceMobileIcon } from "@heroicons/react/outline";
import Button from "./Button";
import {capitalizeFirstLetter} from "../utils";

export default function AddToSigner({ path, color, name }: AddToSignerInterface) {
  const [isOpen, setIsOpen] = useState(false);

  function closeModal() {
    setIsOpen(false);
  }

  function openModal() {
    setIsOpen(true);
  }

  return (
    <>
      <div className="flex items-center mt-5 pt-5 border-t-2 border-gray-200">
        <Button
          className="w-full"
          backgroundColor={color}
          onClick={openModal}
          label={
            <div className="flex justify-center items-center">
              <DeviceMobileIcon className="w-5" />
              <span className="pl-1">Add <b>{capitalizeFirstLetter(name)}</b> to Signer</span>
            </div>
          }
        />
      </div>

      <Transition appear show={isOpen} as={Fragment}>
        <Dialog
          as="div"
          className="fixed inset-0 z-10 overflow-y-auto bg-black/90 backdrop-blur"
          onClose={closeModal}
        >
          <div className="px-4 text-center">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0"
              enterTo="opacity-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100"
              leaveTo="opacity-0"
            >
              <Dialog.Overlay className="fixed inset-0" />
            </Transition.Child>

            {/* This element is to trick the browser into centering the modal contents. */}
            <span
              className="inline-block h-screen align-middle"
              aria-hidden="true"
            >
              &#8203;
            </span>
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 scale-95"
              enterTo="opacity-100 scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 scale-100"
              leaveTo="opacity-0 scale-95"
            >
              <div className="opacity-100 inline-block h-4/5 p-6 my-8 overflow-hidden text-center align-middle transition-all transform bg-white shadow-xl rounded-2xl">
                <Dialog.Title
                  as="h3"
                  className="text-lg font-medium leading-6 text-gray-900"
                >
                  Scan this code with your signer device
                </Dialog.Title>
                <div className="min-h-[180px]">
                  <img
                    src={process.env.PUBLIC_URL + path}
                    className="ml-auto mr-auto"
                    alt="Qr code"
                  />
                </div>

                <div>
                  <button
                    type="button"
                    className="inline-flex justify-center px-4 py-2 text-sm font-medium text-blue-900 bg-blue-100 border border-transparent rounded-md hover:bg-blue-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-blue-500"
                    onClick={closeModal}
                  >
                    Got it, thanks!
                  </button>
                </div>
              </div>
            </Transition.Child>
          </div>
        </Dialog>
      </Transition>
    </>
  );
}
